$main-color: #060b35;
$secondary-color: #616070;
$accent-color: #00acdc;

body {
  margin: 0;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
}

h2,
h3,
p {
  margin: 0;
}

.card {
  box-sizing: border-box;
  width: 200px;
  min-height: 406px;
  padding: 32px 16px 16px;
  border: 1px solid #f3f3f3;
  background-color: #fff;
  border-radius: 5px;
  display: flex;
  flex-direction: column;

  &__img {
    display: inline-block;
    width: 160px;
    height: 134px;
    margin: 0 auto 40px;
  }

  .card__info {

    &-title {
      font-size: 12px;
      font-weight: 500;
      line-height: 18px;
      color: $main-color;
      margin-bottom: 4px;
    }

    &-code {
      font-weight: 400;
      height: 14px;
      font-size: 10px;
      line-height: 14px;
      justify-content: flex-start;
      color: $secondary-color;
      margin-bottom: 16px;
    }

    &-rating {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 24px;

      &-reviews {
        font-size: 10px;
        font-weight: 400;
        color: $main-color;
      }
    }

    &-price {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-weight: 700;
      margin-bottom: 16px;

      &-text {
        font-size: 12px;
        color: $secondary-color;
        font-weight: 400;
      }

      &-number {
        font-size: 16px;
        color: $main-color;
        line-height: 18px;
      }
    }
  }

  &__button {
    display: block;
    height: 40px;
    color: #fff;
    line-height: 40px;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    font-weight: 700;
    font-size: 14px;
    box-sizing: border-box;
    background: $accent-color;
    border-radius: 5px;
  }

  &__button:hover {
    border: 1px solid $accent-color;
    background-color: transparent;
    color: $accent-color;
  }
}

.stars {
  display: flex;

  &__star {
    background: url(../images/star-active.svg) no-repeat center;
    width: 16px;
    height: 16px;
    margin-right: 4px;

    &:nth-last-child(1) {
      background: url(../images/star.svg) no-repeat center;
      margin-right: 0;
    }
  }
}
